//—Ä–∞—Å—á–µ—Ç –≤—Ä–µ–º–µ–Ω–∏ —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞
export function timeCreateBurger(createitemBurger: string): string {
  const minute: number = 1000 * 60;
  const hour: number = minute * 60;
  const day: number = hour * 24;

  let todayDate: Date = new Date(); //—Ç–µ–∫—É—â–∞—è –¥–∞—Ç–∞
  let todayDateISO8601 = todayDate.toISOString(); //–ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º —Ç–µ–∫—É—â—É—é—É –¥–∞—Ç—É –≤ —Ñ–æ—Ä–º–∞—Ç ISO 8601 YYYY-MM-DDTHH:mm:ss.sssZ

  const todayTime = Date.parse(createitemBurger); //–ø–∞—Ä—Å–∏–º –≤—Ä–µ–º—è —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞
  const createTimeBurger = Date.parse(todayDateISO8601); //–ø–∞—Ä—Å–∏–º —Ç–µ–∫—É—â–µ–µ –≤—Ä–µ–º—è
  const days = Math.round((createTimeBurger-todayTime) / day); //–Ω–∞—Ö–æ–¥–∏–º —Ä–∞–∑–Ω–∏—Ü—É –º–µ–∂–¥—É —Ç–µ–∫—É—â–µ–º –≤—Ä–µ–º–µ–Ω–µ–º –∏ –≤—Ä–µ–º–µ–Ω–µ–º —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞
  const createBurgerDay = days != 1 ? '–°–µ–≥–æ–¥–Ω—è' : '–í—á–µ—Ä–∞';
  const createBurg = new Date(createitemBurger);
  const stringCreateBurger = String(createBurg); //–ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –¥–∞—Ç—É —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞ –≤ —Å—Ç—Ä–æ–∫—É
  const IndexElementGreenwichMeanTime = stringCreateBurger.indexOf("GMT"); //–Ω–∞—Ö–æ–¥–∏–º —Å –∫–∞–∫–æ–≥–æ –∏–Ω–¥–µ–∫—Å–∞ –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è GMT
  const GreenwichMeanTime = stringCreateBurger.slice(IndexElementGreenwichMeanTime, IndexElementGreenwichMeanTime+6);

  const createTime: string = (createBurgerDay + ', ' + createBurg.getHours() + ':' + createBurg.getMinutes() + ' i-' + GreenwichMeanTime);
  return createTime;
}
