
import { FC, FormEvent, useCallback } from 'react';
import style, { PasswordInput, Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, useHistory, Redirect } from 'react-router-dom';
import resetPassword from './reset-password.module.css';
import { getCookie } from '../utils/cookie';
import { api } from '../utils/Api';
import { useForm } from '../hooks/useForm';

const ResetPassword: FC = () => {

  const {values, handleChange} = useForm({password: '', token: ''});
  const history = useHistory();
  const forgotPassword: string | undefined = getCookie('forgotPassword');

  //–ï—Å–ª–∏ forgotPassword –Ω–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç –≤ –∫—É–∫–∞—Ö, —Ç–æ –¥–µ–ª–∞–µ–º —Ä–µ–¥–∏—Ä–µ–∫—Ç –Ω–∞ forgot-password –¥–ª—è –≤–≤–æ–¥–∞ email
  if (!forgotPassword) {
    return (
      <Redirect to={{ pathname: '/forgot-password' }} />
    )
  }

  //–ü—Ä–∏ —É—Å–ø–µ—à–Ω–æ–π —Å–º–µ–Ω–µ –ø–∞—Ä–æ–ª—è –¥–µ–ª–∞–µ—Ç —Ä–µ–¥–∏—Ä–µ–∫—Ç –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
  const loginRedirect = useCallback(
    () => {
      history.replace({ pathname: '/login' });
    },
    [history]
  );

  //–ó–∞–ø—Ä–æ—Å –∫ —Å–µ—Ä–≤–µ—Ä—É –¥–ª—è —Å–º–µ–Ω—ã –ø–∞—Ä–æ–ª—è
  const handleClick = useCallback(
    (e: FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      api.resetPassword(values)
        .then(res => {
          if (res.success === true) {
            loginRedirect();
          }
        })
    }, [values]
  )

  return (
    <div className={resetPassword.resetPassword}>
      <h2 className='text text_type_main-medium'>–í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è</h2>
      <div className={`${resetPassword.input_margin}`}>
        <PasswordInput value={`${values?.password}`} onChange={handleChange} name={'password'} />
      </div>
      <form className={`${resetPassword.form}`} onSubmit={handleClick}>
        <div className={`${resetPassword.input_margin}`}>
          <Input
            type={'text'}
            placeholder={'–í–≤–µ–¥–∏—Ç–µ –∫–æ–¥ –∏–∑ –ø–∏—Å—å–º–∞'}
            onChange={handleChange}
            value={`${values?.token}`}
            name={'token'}
            error={false}
            errorText={'–û—à–∏–±–∫–∞'}
            size={'default'}
          />
        </div>
        <div className={resetPassword.button}>
          <Button type="primary" size="medium">
            –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
          </Button>
        </div>
      </form>
      <div className={`${resetPassword.input} text text_type_main-default text_color_inactive`}>
        <p className={resetPassword.paragraf}>–í—Å–ø–æ–º–Ω–∏–ª–∏ –ø–∞—Ä–æ–ª—å?</p>
        <Link to="/login">–í–æ–π—Ç–∏</Link>
      </div>
    </div>
  )
}

export default ResetPassword;
