
import { FC } from 'react';
import style, { CurrencyIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import orders from './orders.module.css';
import { useEffect, useState } from 'react';
import OrderElement from '../orderElement/OrderElement';
import { Link, useLocation } from 'react-router-dom';
import { timeCreateBurger } from '../../utils/time';
import { ICard, IData } from '../../services/types';
import { useAppSelector as useSelector} from '../../services/store/hooks';

const Orders: FC<{ card: ICard }>  = ({ card }) => {

  const [cellOrder, setCellOrder] = useState<number>(0); //—Ü–µ–Ω–∞ –∑–∞ –±—É—Ä–≥–µ—Ä
  const [countData, setCountData] = useState<Array<IData>>([]);
  const [burger, setBurger] = useState<Array<IData>>([]); //–≥–æ—Ç–æ–≤—ã–π –±—É—Ä–≥–µ—Ä
  const location = useLocation();
  const ingredientsData = useSelector((state) => state.getIngredientsApi.ingredientsGetApi); //–≤—Å–µ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã
  const [createTimeBurger, setCreateTimeBurger] = useState<string>(); //–≤—Ä–µ–º—è —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞

  //—Å–±–æ—Ä –¥–∞–Ω–Ω—ã—Ö –æ–± –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–∞—Ö –±—É—Ä–≥–µ—Ä–∞ –≤ –∑–∞–∫–∞–∑–µ
  const createBurger = () => {
    let summa: number = 0; //—Ü–µ–Ω–∞ –∑–∞ –±—É—Ä–≥–µ—Ä
    let arrImage: Array<string> = [];
    let igredientsDetails: Array<IData> = []; //–∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã —Å –ø–æ–¥—Ä–æ–±–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π
    let n: number = 0;
    //—Å–æ–±–∏—Ä–∞–µ–º –∏–∑ –±—É—Ä–≥–µ—Ä–∞ –≤—Å—é –ø–æ–¥–±—Ä–æ–±–Ω—É—é –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –ø–æ –∫–∞–∂–¥–æ–º—É –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—É
    while (n <= card.ingredients.length) {
      ingredientsData.map(item => {
        if (item.id === card.ingredients[n]) {
          summa += item.price;
          arrImage.push(item.image_mobile);
          igredientsDetails.push(item);
        }
      })
      n++;
      setCellOrder(summa); //–ø–µ—Ä–µ–¥–∞–µ–º —Ü–µ–Ω—É –∑–∞ –±—É—Ä–≥–µ—Ä –≤ state
      setCountData(igredientsDetails);
    }

    const nSet = new Set(igredientsDetails); //—Å–æ–∑–¥–∞–µ–º –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
    const uniqueMas = Array.from(nSet); //—Å–æ–∑–¥–∞–µ–º –º–∞—Å—Å–∏–º —É–Ω–∏–∫–∞–ª—å–Ω—ã–π –∑–Ω–∞—á–µ–Ω–∏–π –∏–∑ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞
    const bun = uniqueMas.filter(item => item.type == 'bun'); //–Ω–∞—Ö–æ–¥–∏–º –±—É–ª–æ—á–∫—É
    const createBurger = [...uniqueMas, ...bun]; //–¥–æ–±–∞–≤–ª—è–µ–º –±—É–ª–∫—É –≤ –∫–æ–Ω–µ—Ü –º–∞—Å—Å–∏–≤–∞

    setBurger(createBurger); //–ø–µ—Ä–µ–¥–∞–µ–º –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã —Å –ø–æ–¥—Ä–æ–±–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –≤ state
    setCreateTimeBurger(timeCreateBurger(card.createdAt));//—Ä–∞—Å—á–µ—Ç –≤—Ä–µ–º–µ–Ω–∏ —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞
  }

  useEffect(() => {
    createBurger();
  }, [ingredientsData])

  return (
    <Link className={orders.link}
      to={{
        pathname: `/feed/${card.number}`,
        state: { isOpenModalFeed: location }
      }}
    >
      <li className={orders.orders}>
          <div className={orders.numberOrder}>
            <h2 className={` ${orders.idOrder} text text_type_digits-default `}># {card.number}</h2>
            <p className={` text text_type_main-default text_color_inactive `}>{createTimeBurger}</p>
          </div>
          <p className={` ${orders.burgerName} text text_type_main-medium `}>{card.name}</p>
          <div className={orders.ingredients}>
            <ul className={orders.fillings}>
              {
                burger.map((item, index) => (
                  <OrderElement indexStyle={burger.length - index} burger={burger} item={item} countData={countData} key={index} />
                ))
              }
            </ul>
            <div className={orders.cellPrice}>
              <p className={`${orders.cell} text text_type_digits-medium mr-2`}>{cellOrder}</p>
              <CurrencyIcon type="primary" />
            </div>
          </div>
        </li>
      </Link>
  )
}

export default Orders;
