import React, { FC, useEffect } from 'react';
import style, { CheckMarkIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import orderDetails from './orderDetails.module.css';
import { getOrderDetails } from '../../services/actions/getOrderDetails';
import { getCookie } from '../../utils/cookie';
import { useAppDispatch as useDispatch, useAppSelector as useSelector} from '../../services/store/hooks';

const OrderDetails: FC = () => {

  const selectedId = useSelector(state => state.getOrderDetails.selectedIdIngredients);
  const dispatch = useDispatch();
  const accessToken = getCookie('accessToken');

  useEffect(() => {
    dispatch(getOrderDetails(selectedId, accessToken));
  }, [])

  const feedFailed = useSelector((state) => state.getOrderDetails.feedFailed);
  const feedRequest = useSelector((state) => state.getOrderDetails.feedRequest);
  const order = useSelector(state => state.getOrderDetails.infoOderDetails);

  return (
    <>
      {feedFailed && <p className={` ${orderDetails.numberOrder} text text_type_main-medium`}>–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è –∑–∞–∫–∞–∑–∞</p>}
      {feedRequest && <p className={` ${orderDetails.numberOrder} text text_type_main-medium`}>–§–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏–µ –∑–∞–∫–∞–∑–∞...</p>}
      {!feedFailed && !feedRequest &&
        <>
          <p className={` ${orderDetails.numberOrder} text text_type_digits-large `}>{order[0]?.number}</p>
          <p className={` ${orderDetails.textIdOrder} text text_type_main-medium `}>–∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –∑–∞–∫–∞–∑–∞</p>
          <div className={orderDetails.loadImage}></div>
          <div className={orderDetails.checkImage}><CheckMarkIcon type="primary" /></div>
          <p className={` ${orderDetails.statusOrder} text text_type_main-default `}>–í–∞—à –∑–∞–∫–∞–∑ –Ω–∞—á–∞–ª–∏ –≥–æ—Ç–æ–≤–∏—Ç—å</p>
          <p className={` ${orderDetails.status} text text_type_main-default text_color_inactive `}>–î–æ–∂–¥–∏—Ç–µ—Å—å –≥–æ—Ç–æ–≤–Ω–æ—Å—Ç–∏ –Ω–∞ –æ—Ä–±–∏—Ç–∞–ª—å–Ω–æ–π —Å—Ç–∞–Ω—Ü–∏–∏</p>
        </>
      }
    </>
  )
}

export default OrderDetails;
