//—ç–∫—à–µ–Ω –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª–∫–∏ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç
import { TModals } from "../actions/getIngredientforOpenModal";
import {
  OPEN_SELECTED_INGREDIENT,
  CLOSE_MODALS,
  OPEN_ORDER_DETAILS,
  OPEN_BURGER_DETAILS,
  OPEN_ORDER_ERROR,
} from "../constants"

type TDefaultState = {
  openModal: boolean,
  openModalOrder: boolean,
  openModalError: boolean,
}

const defaultState: TDefaultState = {
  openModal: false, //—Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ—Ç–∫—Ä—ã—Ç–æ –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
  openModalOrder: false, //—Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ—Ç–∫—Ä—ã—Ç–æ –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –Ω–æ–º–µ—Ä–∞ –∑–∞–∫–∞–∑–∞
  openModalError: false, //—Å–æ—Å—Ç–æ—è–Ω–∏–µ –æ—Ç–∫—Ä—ã—Ç–æ –ª–∏ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å –∏–Ω—Ñ–æ–º–∞—Ü–∏–µ–π –æ–± –æ—à–∏–±–∫–µ
}

export const openInfoSelectedIngredient = (state = defaultState, action: TModals): TDefaultState  => {
  switch (action.type) {
    case OPEN_SELECTED_INGREDIENT: {
      return {...state, openModal: true}
    }
    case CLOSE_MODALS: {
      return {...state, openModal: false, openModalOrder: false, openModalError: false}
    }
    case OPEN_ORDER_DETAILS: {
      return {...state, openModalOrder: true}
    }
    case OPEN_BURGER_DETAILS: {
      return {...state, openModalOrder: true}
    }
    case OPEN_ORDER_ERROR: {
      return {...state, openModalError: true}
    }
    default:
      return state
  }
}
