import { FC, useState, useCallback, useEffect } from 'react';
import style, { CurrencyIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import { useParams } from 'react-router-dom';
import { useAppDispatch as useDispatch, useAppSelector as useSelector} from '../../services/store/hooks';
import feedIdDetails from './feedIdDetails.module.css';
import { timeCreateBurger } from '../../utils/time';
import { openBurgerDetails } from '../../services/actions/getIngredientforOpenModal';
import { ICard, ICount, IData } from '../../services/types';

const FeedIdDetails: FC = () => {

  const dispatch = useDispatch();
  const { id }  = useParams<{id: string}>(); //id –±—É—Ä–≥–µ—Ä–∞
  const card = useSelector(state => state.orders.data); //–ø–æ—Å–ª–µ–¥–Ω–∏–µ 50 –∑–∞–∫–∞–∑–æ–≤
  const ingredientsData = useSelector(state => state.getIngredientsApi.ingredientsGetApi); //–≤—Å–µ –≤–æ–∑–º–æ–∂–Ω—ã–µ –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã
  const [cellOrder, setCellOrder] = useState<number>(0); //—Ü–µ–Ω–∞ –∑–∞ –±—É—Ä–≥–µ—Ä
  const [data, setData] = useState<ICard>(); //–Ω–∞–∑–≤–∞–Ω–∏–µ, –Ω–æ–º–µ—Ä –∏ —Ü–µ–Ω–∞ –±—É—Ä–≥–µ—Ä–∞
  const [burger, setBurger] = useState<Array<IData>>([]); //–≥–æ—Ç–æ–≤—ã–π –±—É—Ä–≥–µ—Ä —Å —Ñ–∏–ª—å—Ç—Ä–æ–º –ø–æ —É–Ω–∏–∫–∞–ª—å–Ω—ã–º –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–∞–º
  const [count, setCount] = useState<ICount>({}); //–æ–±—ä–µ–∫—Ç —Ç–∏–ø–∞ {ID:–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ} –≤ –º–∞—Å—Å–∏–≤–µ countData
  const [createTimeBurger, setCreateTimeBurger] = useState<string>(); //–≤—Ä–µ–º—è —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞

  //—Å–±–æ—Ä –¥–∞–Ω–Ω—ã—Ö –æ–± –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç–∞—Ö –±—É—Ä–≥–µ—Ä–∞ –≤ –∑–∞–∫–∞–∑–µ
  const createBurger = useCallback(
    () => {
    const itemBurger = card[0]?.orders.find((item: { number: number; }) => item.number === Number(id)); //–∏—â–µ–º –∏–∑ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö 50 –∑–∞–∫–∞–∑–æ–≤ –Ω–∞—à –ø–æ id
    setData(itemBurger);

    let summa: number = 0; //—Ü–µ–Ω–∞ –∑–∞ –±—É—Ä–≥–µ—Ä
    let arrImage: Array<string> = [];
    let igredientsDetails: Array<IData> = []; //–∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã —Å –ø–æ–¥—Ä–æ–±–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π
    let n: number = 0;
    //—Å–æ–±–∏—Ä–∞–µ–º –∏–∑ –±—É—Ä–≥–µ—Ä–∞ –≤—Å—é –ø–æ–¥–±—Ä–æ–±–Ω—É—é –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –ø–æ –∫–∞–∂–¥–æ–º—É –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—É
    if(itemBurger?.ingredients != undefined) {
      while (n <= itemBurger?.ingredients.length) {
        ingredientsData.map(item => {
          if (item.id === itemBurger.ingredients[n]) {
            summa += item.price;
            arrImage.push(item.image_mobile);
            igredientsDetails.push(item);
          }
        })
        n++;
        setCellOrder(summa); //–ø–µ—Ä–µ–¥–∞–µ–º —Ü–µ–Ω—É –∑–∞ –±—É—Ä–≥–µ—Ä –≤ state
      }
  }

    let result: ICount = {};
    for (let i = 0; i < igredientsDetails.length; ++i) {
      let a: string | undefined = igredientsDetails[i].id;
      if(!a) return;
      if (result[a] != undefined) {
        ++result[a];
      }
      else
        result[a] = 1;
    }
    setCount(result);
    const nSet = new Set<IData>(igredientsDetails); //—Å–æ–∑–¥–∞–µ–º –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
    const createBurger: Array<IData> = Array.from(nSet); //—Å–æ–∑–¥–∞–µ–º –º–∞—Å—Å–∏–º —É–Ω–∏–∫–∞–ª—å–Ω—ã–π –∑–Ω–∞—á–µ–Ω–∏–π –∏–∑ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞
    setBurger(createBurger); //–ø–µ—Ä–µ–¥–∞–µ–º –∏–Ω–≥—Ä–µ–¥–∏–µ–Ω—Ç—ã —Å –ø–æ–¥—Ä–æ–±–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –≤ state

    //—Ä–∞—Å—á–µ—Ç –≤—Ä–µ–º–µ–Ω–∏ —Å–æ–∑–¥–∞–Ω–∏—è –±—É—Ä–≥–µ—Ä–∞
    if(itemBurger?.createdAt != undefined) {
      setCreateTimeBurger(timeCreateBurger(itemBurger?.createdAt));
    }
  }, [card]
  )

  useEffect(() => {
    dispatch(openBurgerDetails());
  }, [])

  useEffect(() => {
    createBurger();
  }, [card])

  return (
    <>
      <div className={feedIdDetails.feedId}>
        <h2 className={` ${feedIdDetails.idOrder} text text_type_digits-default `}># {data?.number}</h2>
        <p className={` ${feedIdDetails.name} text text_type_main-medium `}>{data?.name}</p>
        <p className={` ${feedIdDetails.status} text text_type_main-default `}>{(data?.status === 'done') ? '–í—ã–ø–æ–ª–Ω–µ–Ω' : '–í —Ä–∞–±–æ—Ç–µ'}</p>
        <p className={` ${feedIdDetails.ingredients} text text_type_main-medium `}>–°–æ—Å—Ç–∞–≤:</p>
        <ul className={` ${feedIdDetails.ingredient} `}>
          {burger.map((item, index) => (
            <li key={index} className={` ${feedIdDetails.items} mb-5`}>
              <div className={feedIdDetails.nameIngredient}>
                <div className={` ${feedIdDetails.borderImage} `}>
                  <img className={feedIdDetails.image} src={item?.image_mobile} alt={item?.image_mobile} ></img>
                </div>
                <p className='text text_type_main-default mr-4 ml-4'>{item?.name}</p>
              </div>
              <div className={` ${feedIdDetails.item} `}>
                <p className={`text text_type_digits-default`}>{(count[item.id] > 1) ? (count[item.id]) : '1'}</p>
                <p className='text text_type_digits-default mr-2'>&nbsp;x</p>
                <p className='text text_type_digits-default mr-2'>{`${item?.price}`}</p>
                <CurrencyIcon type="primary" />
              </div>
            </li>
          ))}
        </ul>
        <div className={feedIdDetails.cellPrice}>
            <p className={`text text_type_main-default text_color_inactive`}>{createTimeBurger}</p>
            <div className={feedIdDetails.cell}>
              <p className={`text text_type_digits-default mr-2`}>{cellOrder}</p>
              <CurrencyIcon type="primary" />
            </div>
          </div>
      </div>
    </>
  )
}

export default FeedIdDetails;
