
import { FC, useCallback } from 'react';
import style, { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, useHistory } from 'react-router-dom';
import forgotPassword from './forgot-password.module.css';
import { api } from '../utils/Api';
import { setCookie } from '../utils/cookie';
import { useForm } from '../hooks/useForm';

const ForgotPassword: FC = () => {

  const {values, handleChange} = useForm({email: ''});
  const history = useHistory();

  //–ü—Ä–∏ —É—Å–ø–µ—à–Ω–æ–º –æ—Ç–ø—Ä–∞–≤–∫–µ –∑–∞–ø—Ä–æ—Å–∞ –Ω–∞ –≤–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ
  //–ø–∞—Ä–æ–ª—å –¥–µ–ª–∞–µ–º —Ä–µ–¥–∏—Ä–µ–∫—Ç –Ω–∞ –≤–≤–æ–¥–∞ –Ω–æ–≤–æ–≥–æ –ø–∞—Ä–æ–ª—è –∏ –∫–ª—é—á–∞
  const resetPassword = useCallback(
    () => {
      history.replace({ pathname: '/reset-password' })
    },
    [history]
  );

  //–∑–∞–ø—Ä–æ—Å –∫ —Å–µ—Ä–≤–µ—Ä—É –¥–ª—è –≤–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è –ø–æ email
  const handleClick = useCallback(
    e => {
      e.preventDefault();
      api.getEmails(`${values.email}`)
        .then(res => {
          if (res.success === true) {
            setCookie('forgotPassword', res.success, { expires: 200 });
            resetPassword();
          }
        })
    },
    [values]
  )

  return (
    <div className={forgotPassword.forgotPassword}>
      <h2 className='text text_type_main-medium'>–í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è</h2>
      <form className={forgotPassword.form}>
        <div className={forgotPassword.email}>
          <Input
            type={'email'}
            placeholder={'–£–∫–∞–∂–∏—Ç–µ e-mail'}
            onChange={handleChange}
            value={`${values.email}`}
            name={'email'}
            error={false}
            errorText={'–û—à–∏–±–∫–∞'}
            size={'default'}
          />
        </div>
        <div className={forgotPassword.button}>
          <Button onClick={handleClick} type="primary" size="medium">
            –í–æ—Å—Å—Ç–∞–Ω–æ–≤–∏—Ç—å
          </Button>
        </div>
      </form>
      <div className={`${forgotPassword.input} text text_type_main-default text_color_inactive`}>
        <p className={forgotPassword.paragraf}>–í—Å–ø–æ–º–Ω–∏–ª–∏ –ø–∞—Ä–æ–ª—å?</p>
        <Link className={forgotPassword.link} to="/login">–í–æ–π—Ç–∏</Link>
      </div>
    </div>
  )
}

export default ForgotPassword;
