
import { FC, useRef, useCallback, FormEvent } from 'react';
import style, { PasswordInput, Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, useHistory } from 'react-router-dom';
import register from './register.module.css';
import { api } from '../utils/Api';
import { useForm } from '../hooks/useForm';

const Register: FC = () => {

  const {values, handleChange} = useForm({email: '', password: '', name: ''});
  const inputRef = useRef<HTMLInputElement>(null)
  const history = useHistory();

  //–ü—Ä–∏ —É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏, –¥–µ–ª–∞–µ–º —Ä–µ–¥–∏—Ä–µ–∫—Ç –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –∞—Ç–≤–æ—Ä–∏–∑–∞—Ü–∏–∏
  const loginRequest = useCallback(() => {
    history.replace({ pathname: '/login' })
  },
    [history]
  );

  //–ó–∞–ø—Ä–æ—Å –∫ —Å–µ—Ä–≤–µ—Ä—É –¥–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  const handleClick = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    api.register(values)
      .then(res => {
        if(res.success === true) {
          loginRequest();
        }
      })
  }

  return (
    <div className={register.register}>
      <h2 className='text text_type_main-medium'>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
      <form className={register.form} onSubmit={handleClick}>
        <div className={`${register.input_margin}`}>
          <Input
            type={'text'}
            placeholder={'–∏–º—è'}
            onChange={handleChange}
            value={`${values?.name}`}
            name={'name'}
            error={false}
            ref={inputRef}
            errorText={'–û—à–∏–±–∫–∞'}
            size={'default'}
          />
        </div>
        <div className={register.email}>
          <Input
            type={'email'}
            placeholder={'E-mail'}
            onChange={handleChange}
            value={`${values?.email}`}
            name={'email'}
            error={false}
            errorText={'–û—à–∏–±–∫–∞'}
            size={'default'}
          />
        </div>
          <PasswordInput onChange={handleChange} value={`${values?.password}`} name={'password'} />
        <div className={register.button}>
          <Button type="primary" size="medium" >
            –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
          </Button>
        </div>
      </form>
      <div className={`${register.input} text text_type_main-default text_color_inactive`}>
        <p className={register.paragraf}>–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã?</p>
        <Link className={register.link} to="/login">–í–æ–π—Ç–∏</Link>
      </div>
    </div>
  )
}

export default Register;
